import { useEffect, useState } from "react";
const quotes=[
    "   .       .",
    "   ,     .",
    "  .       .",
    "       .",
    "       90%,     10% .",
    "       .",
    "     .",
    "  .   .",
    "   ,      .",
    " .      !"
];

const Quote = () => {
    const [quote,setQuote] = useState('');
    //  
    // Math.random(); //0~1  : 0 ~ quotes.laegth
    useEffect(()=>{
        const random = Math.floor(Math.random()*quotes.length);
        setQuote(quotes[random]);
    },[])
    
    return (
        <div className="quote">
            "{quote}"
        </div>
    );
};

export default Quote;