import { useEffect, useState } from "react";
import "./App.scss";
import LoginForm from "./Components/LoginForm";
import Time from "./Components/Time";
import MainPage from "./Components/MainPage";
import { click } from "@testing-library/user-event/dist/click";
import Weather from "./Components/Weather";
import Dark from "./Components/Dark";
// import bgImg from './images/img.jpg'; //  

const App = () => {
  const USER_KEY = "user_name";
  const [user,setUser] = useState('');
  const [dark,setDark] = useState(false);
  useEffect(()=>{
    const savedDark = localStorage.getItem("darkMode");
    if(savedDark !== null){
      setDark(savedDark === "true");
    }
  },[])
  useEffect(()=>{
    localStorage.setItem("darkMode",dark.toString());
    if(dark){
      document.body.classList.add("dark");
    } else{
      document.body.classList.remove("dark");
    }
  },[dark])
  //   user_name  
  useEffect(()=>{
    const saved = localStorage.getItem(USER_KEY);
    if(saved){
      setUser(saved);
    }
  },[])
  const handleLogin = (data)=>{
    localStorage.setItem(USER_KEY,data);
    setUser(data);
  }
  const handleLogout =()=>{
    localStorage.removeItem(USER_KEY);
    setUser(null);
  }
    const [greeting,setGreeting] = useState('');
      useEffect(()=>{
        const now = new Date();  //  
        const hour = now.getHours(); //0 23   
          if(hour >= 5 && hour < 12 ){
            setGreeting(" !")
          } else if(hour >= 12 && hour < 18 ){
            setGreeting(" !")
          } else if(hour >= 18 && hour < 22 ){
            setGreeting(" !")
          } else{ setGreeting("  !")}
        },[]);
    const toggleDark = ()=>{
    setDark(prev => !prev);
    }
  return (
    <div className="app-nav">
    <Dark onDark={toggleDark} dark={dark}/>
    <Weather/>
    <div className="app">
      {/* <img src="./images/img.jpg" alt="1"/> */}
      {/* <img src={`${process.env.PUBLIC_URL}/images/img.jpg`} alt="1"/> */}
      {/* <img src={bgImg} alt="1"/> */}
      <Time/>
      {
        user ? (<MainPage user={user} onLogout={handleLogout} greeting={greeting}/>) : (<LoginForm onLogin={handleLogin}/>)
      }
      </div>
      </div>
  );
};

export default App;