import { useEffect, useState } from "react";
import Quote from "../Quote";
import TodoForm from "./TodoForm";
import TodoList from "./TodoList";

const MainPage = ({user,onLogout,greeting}) => {
    const TODOS_KEY="todos";
    const [todos,setTodos] = useState([]);
    // localStrage  todos   
    useEffect(()=>{
        const saved = localStorage.getItem(TODOS_KEY);
        if(saved){
            setTodos(JSON.parse(saved)); //   
        }
    },[]);
    //todos  localStrage 
    useEffect(()=>{
        const saved = JSON.stringify(todos); //   ...
        localStorage.setItem(TODOS_KEY,saved);
    },[todos]);
    const addTodo = (text)=>{
        /**
         * todo{ id:Date.now()(  ), text:} 
         *   ( )
         */
        const newTodo = {id:Date.now(), todo:text, done:false};
        setTodos([...todos,newTodo]);
    }
    const deleteTodo = (id)=>{ //todos   
        const update = todos.filter((item)=>{
            return item.id !== id;
        });
        setTodos(update);
    }
    const toggleTodo = (id)=>{
    const update = todos.map((item)=>{
    return item.id === id ? {...item, done:!item.done} : item;
    });
    setTodos(update);
    }

    return (
        <div className="main">
            <div className="main-user">
            <h2>{user}, {greeting}</h2>
            <button onClick={onLogout}>Logout</button>
            </div>
        <Quote/>
        <div className="todo">
        <TodoForm onAdd={addTodo}/>
        <TodoList todos={todos} onDelete={deleteTodo} onToggle={toggleTodo}/>
        </div>
        </div>
    );
};

export default MainPage;
